// Education
.page5 {
  color: white;
  grid-row: 5/6;
  display: none;
  @media screen and (min-width: $break-small) {
    display: flex;
  }
  flex-direction: row;
  justify-content: center;
  align-items: center;
  .scroll-icon {
    position: relative;
    left: 50%;
    bottom: 46%;
    border-radius: 20px;
    height: 30px;
    z-index: 1000;
  }
  .image-scroll {
    margin: 0 auto;
    margin-top: 5%;
    height: 30px;
    width: 30px;
    -webkit-transition: -webkit-transform 0.8s ease-in-out;
    transition: transform 0.8s ease-in-out;
  }
  .image-scroll:hover {
    -webkit-transform: rotate(180deg);
    transform: rotate(180deg);
  }
  .education-wrapper {
    height: 80%;
    width: 80%;
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    .education-left {
      width: 50%;
      height: 100%;
      p {
        width: 70%;
      }
      .education-icons {
        padding: 24px;
        text-align: center;
        font-size: 14px;
        width: 90%;
        height: 50%;
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr;
        grid-template-rows: 1fr 1fr;
        position: relative;
        right: 8%;
        .icon-card {
          padding: 8%;
        }
      }
    }
    .education-right {
      border: 1px solid rgba(233, 233, 233, 0.4);
      width: 50%;
      height: 100%;
      padding: 48px;
      ul {
        padding: 0;
        margin: 0;
      }
      li {
        font-size: 13px;
        margin: 0;
        margin-bottom: 14px;
        list-style: none;
      }
      ul li::before {
        color: red;
        content: "-";
        padding-right: 1.1225em;
        position: relative;
        top: 0em;
      }
    }
  }
}

// Contact
.page6 {
  grid-row: 6/7;
  display: none;
  @media screen and (min-width: $break-small) {
    display: flex;
  }
  flex-direction: column;
  padding-top: 10%;
  .contact-wrap {
    justify-content: space-between;
    align-items: space-between;
    height: 70%;
    width: 90%;
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    .contact-me {
      height: 30%;
      width: 80%;
      color: white;
      padding-left: 44px;
      a {
        color: white;
        text-decoration: none;
        text-shadow: 1px 1px 1px blue;
      }
      a:hover {
        color: rgba(0, 68, 255, 0.63);
        text-decoration: none;
        text-shadow: 2px 2px 2px blue;
      }
    }

    .contact-form {
      display: flex;
      flex-direction: row;
      height: 50%;
      width: 90%;
      padding: 34px;
      padding-left: 24px;
      align-items: center;
      justify-content: center;
      p {
        color: white;
        display: inline-block;
        width: 24%;
        margin-right: 14px;
        opacity: 0.8;
        margin-bottom: 14px;
      }
      .wpcf7-form-control {
        width: 100%;
      }
      .wpcf7-textarea {
        width: 100%;
        height: 100px;
        margin-right: 44px;
      }
      .wpcf7-submit {
        border-radius: 0;
        border: none;
        padding: 8px;
        background-color: white;
        box-shadow: none;
        text-shadow: none;
        width: 124px;
        text-transform: uppercase;
        margin-top: 34px;
        margin-bottom: 44px;
        position: relative;
        left: 44px;
        bottom: 12px;
        color: red;
      }
      .wpcf7-submit:hover {
        box-shadow: 0 0 5px white;
        position: relative;
      }
      .wpcf7-validation-errors {
        background-color: rgb(255, 255, 255);
        color: rgb(82, 1, 1);
        font-weight: 400;
        border: none;
        margin: 0;
        padding: 0;
      }
      .wpcf7-mail-sent-ok {
        background-color: rgb(6, 177, 0);
        color: rgb(255, 255, 255);
        font-weight: 400;
        margin: 0;
        border: none;
        padding: 0;
      }
    }
  }
}
